import React,{useState,useEffect} from 'react'
import {Form,Input,message} from 'antd'
import { Link,useNavigate } from 'react-router-dom'
import axios from 'axios'
import Spinner from '../components/Spinner'
import "../styles/RegisterPage.css"
const Register = () => {

  const navigate=useNavigate() // this is sused to navigate from one page to another page
  const [loading,setloading]=useState(false)
// form submit
     const submitHandler=async(values)=>{ // this function is responsible to send the values to mongoDB
        try {
          setloading(true);
          await axios.post("/api/v1/users/register",values)
          message.success("Registration Successful")
          setloading(false);
          navigate("/login")
        } catch (error) {
          setloading(false);
          message.error("something went wrong")
        }
     }

  // if the user is logged in and again tries to login in then the user is navigated back to the homepage itself
  useEffect(()=>{
    if(localStorage.getItem('user'))
    {
      navigate('/')
    }
  },[navigate])


  return (
    <>
    <div className="register-page">
    {loading && <Spinner/>}
    <Form className="register-form" layout="verticle" onFinish={submitHandler}>
        <h2>
            Register Form
        </h2>
        <Form.Item label="Name" name="name"><Input/></Form.Item>
        <Form.Item label="Email" name="email"><Input type="email"/></Form.Item>
        <Form.Item label="Password" name="password"><Input type="password"/></Form.Item>
        <div className="d-flex justify-content-between">
            <Link to="/login">Already Registered? Click here to login</Link>
            <button className="btn btn-primary">Register</button>
        </div>
        </Form>
      </div>
      </>
  )
}

export default Register
