import React,{useState,useEffect} from 'react'
import {DatePicker,Form, Input, Modal, Select, Table, message} from 'antd'
import Layout from '../components/Layouts/Layout'
import axios from 'axios'
import Spinner from '../components/Spinner'
import moment from "moment"
import {UnorderedListOutlined,AreaChartOutlined,EditOutlined,DeleteOutlined} from '@ant-design/icons'
import Analytics from '../components/Analytics'
const {RangePicker}=DatePicker;


const HomePage = () => {
  const [showModal,setShowModal]=useState(false)  // this is for the FORM of ADD NEW TASK
  const [loading,setLoading]=useState(false)
  const [allTransaction,setAllTransaction]=useState([])
  const [frequency,setFrequency]=useState('7') // this usestate is for 7,30,365 days range
  const [selectedDate,setSelectedate]=useState([])  // this is for CUSTOM date ranges
  const [type,setType]=useState('all')           // type of transaction
  const [viewData,setViewData]=useState('table')
  const [editable,setEditable]=useState(null)

  //table data
  const columns=[
    {
      title:"Date",
      dataIndex:"date",
      render:(text)=><span>{moment(text).format("YYYY-MM-DD")}</span>
    },
    {
      title: "Amount",
      dataIndex: "amount"
    },
    {
      title:"Type",
      dataIndex:"type"
    },
    {
      title: "Category",
      dataIndex: "category"
    },
    {
      title: "Reference",
      dataIndex: "reference"
    },
    {
      title: "Actions",
      render:(text,record)=>(
        <div>
          <EditOutlined onClick={()=>{ setEditable(record)  // edit button for task
            setShowModal(true)
          }
          } />
          <DeleteOutlined className="mx-2" onClick={()=>handleDelete(record)}/>  
        </div>
      )
    }
  ]

  

  // useEffect Hook
  useEffect(()=>{
    // get all transactions
    const getAllTransactions = async () => {
      try {
        const user = JSON.parse(localStorage.getItem('user'))
        setLoading(true)
        const res = await axios.post('/api/v1/transactions/get-transaction', {
          userid: user._id,
          frequency,
          selectedDate,
          type
        })
        setLoading(false)
        setAllTransaction(res.data)
        console.log(res.data)
      } catch (error) {
        console.log(error)
        message.error("Fetch issue with transaction")
      }
    }
    getAllTransactions();
  },[frequency,selectedDate,type,setAllTransaction])  // frequency is for 7,30,365 days range but the selectedDate is for custom date range


  // delete handler
  const handleDelete=async(record)=>{  // here values is the parameter which gets the record as values when this function is called
    try {
      setLoading(true)
      await axios.post('/api/v1/transactions/delete-transaction', {
        transactionId: record._id
      })
      setLoading(false)
      message.success("Transaction Deleted")
    } catch (error) {
      setLoading(false)
      console.log(error)
      message.error('Unable to Delete')
    }
  }

  // form handling
  const handleSubmit=async(values)=>{ // this function 
    try {
      const user=JSON.parse(localStorage.getItem('user'))
      setLoading(true)
      if(editable){
        await axios.post('/api/v1/transactions/edit-transaction', {
          payload:{
            ...values,
            userId:user._id
          },
          transactionId:editable._id
        })
        setLoading(false)
        message.success("Transaction Updated Successfully")
      }
      else{
        await axios.post('/api/v1/transactions/add-transaction', {
          ...values,
          userid: user._id
        })
        setLoading(false)
        message.success("Transaction Added Successfully")
      }
      setShowModal(false)
      setEditable(null)
    } catch (error) {
      setLoading(false)
      message.error("Please Fill All the Fields")
    }
  }

  return (
    <Layout>
      {loading && <Spinner/>}
      <div className="filters">
        <div>
          <h6>Select Frequency</h6> 
          <Select value={frequency} onChange={(values)=>setFrequency(values)}>
            <Select.Option value="7">LAST 1 WEEK</Select.Option>
            <Select.Option value="30"> LAST 1 MONTH </Select.Option>
            <Select.Option value="365"> LAST 1 YEAR </Select.Option>
            <Select.Option value="custom"> CUSTOM </Select.Option>
          </Select>
          {frequency === "custom" && (
          <RangePicker 
          value={selectedDate} 
          onChange={(values)=>setSelectedate(values)}
        />
        )}
        </div>
        <div className="filter-tab">
          <h6>Select Type</h6>
          <Select value={type} onChange={(values)=>setType(values)}>
            <Select.Option value="all">ALL</Select.Option>
            <Select.Option value="income"> INCOME </Select.Option>
            <Select.Option value="expense"> EXPENSE </Select.Option>
            
          </Select>
          {frequency === "custom" && (
          <RangePicker 
          value={selectedDate} 
          onChange={(values)=>setSelectedate(values)}
        />
        )}
        </div>
        {/* <div className="switch-icons">
            <UnorderedListOutlined  // this is the icon for table unordered list 
            className={`mx-2 ${viewData==='table' ? 'active-icon': 'inactive-icon'}`} 
            onClick={()=>setViewData('table')}
            />
            {<AreaChartOutlined 
            className={`mx-2 ${viewData==='analytics' ? 'active-icon': 'inactive-icon'}`} 
            onClick={()=>setViewData('analytics')}
            />}
          </div> */}
        <div>
          <button
          className="btn btn-primary" 
          onClick={()=>setShowModal(true)}
          >
            Add New Expense
          </button>
        </div>
        </div>



      <div className="content"> 
        {viewData==='table' ? 
        <Table columns={columns} dataSource={allTransaction}/>  // this is the home page ---TABLE--- which shows all the transactions
         : <Analytics allTransaction={allTransaction} />
      }
      </div>
  

  
      <Modal title={editable ? 'Edit Expense':'Add Expense'} 
      open={showModal} 
      onCancel={()=>setShowModal(false)}
      footer={false}
      >
        <Form layout="verticle" onFinish={handleSubmit} initialValues={editable}>
          <Form.Item label="Amount" name="amount">
            <Input type="text" />
          </Form.Item>
          <Form.Item label="type" name="type">
            <Select>
              <Select.Option value="income"> Income </Select.Option>
              <Select.Option value="expense"> Expense </Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Category" name="category">
            <Select>
              <Select.Option value="salary"> Salary </Select.Option>
              <Select.Option value="tip"> Tip </Select.Option>
              <Select.Option value="project"> Project </Select.Option>
              <Select.Option value="food"> Food </Select.Option>
              <Select.Option value="movie"> Movie </Select.Option>
              <Select.Option value="bills"> Bills </Select.Option>
              <Select.Option value="medical"> Medical </Select.Option>
              <Select.Option value="fee"> Fee </Select.Option>
              <Select.Option value="tax"> Tax </Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Date" name="date">
            <Input type="date"/>
          </Form.Item>
          <Form.Item label="Reference" name="reference">
            <Input type="text"/>
          </Form.Item>
          <Form.Item label="Description" name="description">
            <Input type="text"/>
          </Form.Item>
          <div className="d-flex justify-content-end">
            <button type="submit" className="btn btn-primary">{" "}SAVE</button>
          </div>
        </Form>
      </Modal>
    </Layout>
  )
}

export default HomePage
